* {
    font-family: Helvetica, Arial, sans-serif;
    background-color: rgb(234, 234, 234);
  }
  
  body {
    padding: 60px;
  }
  
  input {
    background-color: white;
    border-radius: 5px;
    border: 1px solid rgb(97, 32, 158);
    color: rgb(70, 70, 70);
    font-size: 1rem;
    outline: none;
    padding: 5px 10px;
    width: 100%;
  }
  
  h2 {
    font-size: 1.5rem;
  }
  
  h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    padding: 10px;
    color: #f1f1f1;
    border-bottom: solid 5px #2c2a31;
    background-color: #4b4848;
    text-align: center;
    margin-top: 50px;
    text-transform: uppercase;
  }
  
  .card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .card {
    align-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.03);
    display: flex;
    flex-direction: column;
    height:0%;  
    justify-content: center;
    margin-bottom: 20px;
    margin-right: 20px;
    overflow: hidden;
  }
  
  .card p {
    background-color: white;
    font-size: 15px;
    margin-top: 5px;
    padding: 5px 0 0;
    text-align: center;
    height: 20px;
    overflow: hidden;
  }
  
  .card img {
    width: auto;
    height: 200px;
  }

